<template>
  <div id="app">  
    <div class="card">
    <div class="card-header" style ="font-weight: bold; font-size: xx-large;">TAKIDA's HOUSE</div>
    <div class="card-body row">      
      <Calendar ref="cal"
            language="es"
            render-style="background"
            :enable-range-selection="true"
            :data-source="dataSource"
            :enable-context-menu="false"
            :context-menu-items="contextMenuItems"
            :min-date="minDate"
            @mouse-on-day="mouseOnDay" 
            @mouse-out-day="mouseOutDay"
            @select-range="selectRange">
  </Calendar>
      </div>
  </div>

  
<b-modal :title="currentId != null ? 'Editar evento' : 'A침adir evento'" ok-title="Guardar" v-model="show" @ok="saveEvent">
    <form class="form-horizontal">
      <div class="form-group row">
        <label for="event-name" class="col-sm-2 control-label">Nombre</label>
        <div class="col-sm-10">
          <input id="event-name" type="text" class="form-control" v-model="currentName" />
        </div>
      </div>
      <div class="form-group row">
        <label for="event-location" class="col-sm-2 control-label">Notas</label>
        <div class="col-sm-10">
          <input id="event-location" type="text" class="form-control" v-model="currentLocation" />
        </div>
      </div>
      <div class="form-group row">
        <label for="min-date" class="col-sm-2 control-label">Fechas</label>
        <div class="col-sm-10">
          <div class="input-group input-daterange">
            <input id="min-date" type="date" class="form-control" v-model="currentStartDate" />
            <div class="input-group-prepend input-group-append">
              <div class="input-group-text">a</div>
            </div>
            <input type="date" class="form-control" v-model="currentEndDate" />
          </div>
        </div>
      </div>
    </form>
  </b-modal>

  </div>
</template>

<script>
import Calendar from 'v-year-calendar'

//IIB
import 'v-year-calendar/locales/v-year-calendar.es'

import moment from 'moment'
import tippy from 'tippy.js'

import 'tippy.js/dist/tippy.css'
import 'tippy.js/animations/shift-away.css'

//IIB ini-Firebse
import * as firebase from 'firebase/app'
import 'firebase/database'

let config = {
   //Datos de configuraci칩n
    apiKey: "AIzaSyB6LgZ0e26Xjt1VdYdQIquY2Imdc2WgRiY",
    authDomain: "takida-calendar.firebaseapp.com",
    databaseURL: "https://takida-calendar.firebaseio.com",
    projectId: "takida-calendar",
    storageBucket: "takida-calendar.appspot.com",
    messagingSenderId: "943694385020",
    appId: "1:943694385020:web:e649804818dd9550a75ef9"
}

let app = firebase.initializeApp(config);

let reservas_db = app.database().ref("reservas")

//IIB fin firebase

export default {
  name: 'App',
  components: {
    Calendar
  },
  data: function() {
    return {
      show: false,
      dataSource: [],
      currentId: null,
      currentStartDate: null,
      currentEndDate: null,
      currentName: null,
      currentLocation: null,
      tooltip: null,
      minDateString: '2020-01-01',
      contextMenuItems: [
        {
          text: "Actualizar",
          click: evt => {
            this.currentId = evt.id;
            //IIB Cabio formato en fecha
            this.currentStartDate = moment(evt.startDate).format('YYYY-MM-DD');
            this.currentEndDate = moment(evt.endDate).format('YYYY-MM-DD');
            this.currentName = evt.name;
            this.currentLocation = evt.location;
            //IIB solo muestro la ventana de a침adir  a partir de hoy 
            this.show=true;
            //this.show=(this.currentStartDate >= moment(Date()).format('YYYY-MM-DD'));
          }
        },
        {
          text: "Borrar",
          click: evt => {
            firebase.database().ref('reservas/' + evt.id).remove();
          }
        }
      ]
    };
  },
  computed: {
    minDate: function() {
      return this.minDateString != null ? new Date(this.minDateString) : null;
    }
  },
  created: function() {
    let self = this;
       
    reservas_db.on("value", function(snapshot) {
      let reservas = snapshot.val();
      // Create a new array with all the reservations
      let ds = [];
      Object.values(reservas).map(reserva => Object.assign({}, reserva, {
              startDate: new Date(Date.parse(reserva.startDate)),
              endDate: new Date(Date.parse(reserva.endDate))
            }))
            .forEach(reserva => ds.push(reserva));
      // BEGIN Dirty hack
      if (ds.length === self.dataSource.length) {
        ds.push({id:""});
      }
      // END Dirty hack
      //console.log("EVENTS UPDATED", ds);
      self.dataSource = ds;
    });
  },
  updated: function() {
    let vcal = this.$refs.cal;
    let cells = vcal.$el.querySelectorAll('.day:not(.old):not(.new):not(.disabled)');
    cells.forEach(cell => {
			cell.addEventListener('contextmenu', e => {
        let date = vcal.calendar._getDate(e.currentTarget);
        if (!moment(date).isBefore(moment())) {
					e.preventDefault();
					if (vcal.calendar.options.contextMenuItems.length > 0) {
						vcal.calendar._openContextMenu(e.currentTarget);
					}
				}
      });
    });
  },
  methods: {
    showContext: function() {
      return (this.currentStartDate >= moment(Date()).format('YYYY-MM-DD')); 
           
    },

    //ini-IIB Tooltip
    mouseOnDay: function(e) {



      if (e.events.length > 0) {
        var content = '';

        for (var i in e.events) {
          content += '<div class="event-tooltip-content">'
            + '<div class="event-name" style="color:' + e.events[i].color + '">' + e.events[i].name + '</div>'
            + '<div class="event-details">' + e.events[i].location + '</div>'
            + '</div>';
        }

        if (this.tooltip != null) {
          this.tooltip.destroy();
          this.tooltip = null;
        }

        this.tooltip = tippy(e.element, {
           placement: 'right',
            content: content,
            animation: 'shift-away',
            arrow: true
        });
        this.tooltip.show();
      }
    },
    mouseOutDay: function() {
      if (this.tooltip !== null) {
        this.tooltip.destroy();
        this.tooltip = null;
      }
    },
     //fin-IIB
    selectRange: function(e) {
      this.currentId = null;
      this.currentName = null;
      this.currentLocation = null;
      //IIB Cabio formato en fecha
      this.currentStartDate = moment(e.startDate).format('YYYY-MM-DD');
      this.currentEndDate = moment(e.endDate).format('YYYY-MM-DD');
      this.show = !moment(e.startDate).isBefore(moment());
    },
    saveEvent: function() {
      if (this.currentId == null) {
        // Add event
        let newId = firebase.database().ref('reservas').push().key;
        let reserva = {
          id: newId,
          startDate: moment(this.currentStartDate).format('YYYY-MM-DD'),
          endDate: moment(this.currentEndDate).format('YYYY-MM-DD'),
          name: this.currentName,
          location: this.currentLocation,
        };
        firebase.database().ref('reservas/' + newId).set(reserva);
      } else {
        // Update event
        let id = this.currentId;
        firebase.database().ref('reservas/' + id).set({
          id: id,
          startDate: moment(this.currentStartDate).format('YYYY-MM-DD'),
          endDate: moment(this.currentEndDate).format('YYYY-MM-DD'),
          name: this.currentName,
          location: this.currentLocation
         });
      }
    }
  }
}
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

</style>
